.footer{
    display: flex;
    position: relative;
    background: black; 
    color:white;
    width:100%;
    text-align: center;
    height: 100px;
}


.right-web-name{
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-left: 20px;
    height: 100%;
}

.right-web-name img{
    margin-right: 20px;
    width: 50px;
    border-radius: 50px;
    border: 1px solid white;
    box-shadow: 0 0 10px white;
}

.right-web-name hr{
    height: 70%;
    background: black;
}

.copyText{
    margin-left: 20px;
    border-bottom: 1px solid rgba(255, 255, 255, 0.7);
    color: rgba(255, 255, 255, 0.7);
}

.copyText:hover{
    color: white;
}

.social-media{
    display: flex;
    position: absolute;
    right: 0;
    margin-right: 10px;
    justify-content: center;
    align-items: center;
    height: 100%;   
}

@media screen and (max-width:800px) {
    .footer{
        flex-direction: column;
    }
    .social-media{
        align-items: flex-end;
    }
}