

@keyframes hide-diw-d {
    0%{
        visibility: visible;
    }
    100%{
        visibility: hidden;
    }
}

.main-dival{
    position: fixed !important;
    background:rgba(0, 0, 0, 0);
    /* background:red; */
    background-repeat: no-repeat;
    z-index: 50;
    height: 100vh;
    visibility: hidden;
    animation-name: hide-diw-d;
    animation-duration: 10s;
    /* animation-delay: 3s; */
}


.Diw-container {
    width: 100px;
    float: left;
    padding: 1rem;
    background-color: rgba(0, 0, 0, 0);
}

.box {
    width: 100px;
    height: 100px;
    border-radius: 50%;
    position: relative;
    -webkit-animation: move-up 2s forwards 10s;
    animation-iteration-count: 3;
    animation: move-up 2s forwards 10s;
    margin: 0 auto;
}

.box:after {
    width: 1px;
    content: "";
    height: 5px;
    position: absolute;
    bottom: 0;
    display: block;
    left: 50%;
    -webkit-animation: fadeout 1s forwards;
    animation: fadeout 1s forwards;
    animation-iteration-count: 3;
    -webkit-animation-delay: 1s;
    animation-delay: 1s;
}

.box.m3 {
    -webkit-animation: move-up-3 2s forwards;
    animation-delay: 3s;
    animation: move-up-3 2s forwards;
    animation-iteration-count: 3;

}

.box.m3 {
    -webkit-animation: move-up-2 2s forwards;
    animation: move-up-2 2s forwards;
    animation-iteration-count: 3;

}

.cracker.solid {
    border: solid #ffe1c8 1px;
}

.cracker.dotted {
    border: dotted #ffcd64 10px;
}

.cracker {
    position: absolute;
    border: dashed #fffffa 2px;
    width: 100px;
    height: 100px;
    opacity: 0;
    border-radius: 50%;
    -webkit-animation: burst 6s 1 2s;
    animation-iteration-count: 3;
    animation: burst 6s 1 2s;

}

.a1 {
    -webkit-animation-duration: 4s;
    animation-duration: 4s;
}

.d1 {
    -webkit-animation-delay: 5s;
    animation-delay: 5s;
}

.a2 {
    -webkit-animation-duration: 2s;
    animation-duration: 2s;
}

.d2 {
    -webkit-animation-delay: 8s;
    animation-delay: 8s;
}

.a3 {
    -webkit-animation-duration: 3s;
    animation-duration: 3s;
}

.d3 {
    -webkit-animation-delay: 12s;
    animation-delay: 12s;
}

.a4 {
    -webkit-animation-duration: 1s;
    animation-duration: 1s;
}

.d4 {
    -webkit-animation-delay: 15s;
    animation-delay: 15s;
}

.m1 {
    -webkit-animation-delay: 100ms;
    animation-delay: 100ms;
}

.m2 {
    -webkit-animation-delay: 300ms;
    animation-delay: 300ms;
}

.m3 {
    -webkit-animation-delay: 600ms;
    animation-delay: 600ms;
}

@-webkit-keyframes move-up {
    0% {
        transform: translateY(500px);
    }
    50% {
        transform: translateY(500px);
    }
    100% {
        transform: translateY(100px);
    }
}

@keyframes move-up {
    0% {
        transform: translateY(500px);
    }
    50% {
        transform: translateY(500px);
    }
    100% {
        transform: translateY(100px);
    }
}
@-webkit-keyframes move-up-2 {
    0% {
        transform: translateY(500px);
    }
    50% {
        transform: translateY(500px);
    }
    100% {
        transform: translateY(150px);
    }
}
@keyframes move-up-2 {
    0% {
        transform: translateY(500px);
    }
    50% {
        transform: translateY(500px);
    }
    100% {
        transform: translateY(150px);
    }
}
@-webkit-keyframes move-up-3 {
    0% {
        transform: translateY(500px);
    }
    50% {
        transform: translateY(500px);
    }
    100% {
        transform: translateY(220px);
    }
}
@keyframes move-up-3 {
    0% {
        transform: translateY(500px);
    }
    50% {
        transform: translateY(500px);
    }
    100% {
        transform: translateY(220px);
    }
}
@-webkit-keyframes fadeout {
    0% {
        background: rgba(255, 250, 255, 0.8);
    }
    50% {
        background: rgba(251, 250, 255, 0.8);
        height: 100px;
    }
    100% {
        background: rgba(0, 0, 0, 0);
        height: 120px;
    }
}
@keyframes fadeout {
    0% {
        background: rgba(255, 250, 255, 0.8);
    }
    50% {
        background: rgba(251, 250, 255, 0.8);
        height: 100px;
    }
    100% {
        background: rgba(0, 0, 0, 0);
        height: 120px;
    }
}
@-webkit-keyframes burst {
    0% {
        transform: scale(0);
        opacity: 1;
    }
    50% {
        transform: scale(0);
        opacity: 1;
    }
    100% {
        transform: scale(2);
        opacity: 0;
    }
}
@keyframes burst {
    0% {
        transform: scale(0);
        opacity: 1;
    }
    50% {
        transform: scale(0);
        opacity: 1;
    }
    100% {
        transform: scale(2);
        opacity: 0;
    }
}
.land {
    position: fixed;
    bottom: 0;
    width: 100%;
    display: flex;
    align-items: flex-end;
}

.block {
    background: black;
    width: 30px;
    height: 40px;
    float: left;
    bottom: 0;
}

.block:after {
    display: block;
    border: yellow dotted 10px;
    content: "";
    height: 50px;
    width: 8px;
}

.block:before {
    display: block;
    border: gold dotted 10px;
    content: "";
    height: 90px;
    width: 10px;
}

.p0 {
    padding: 0.5rem;
}

.p1 {
    padding: 1rem;
}

.p2 {
    padding: 2rem;
}

.p3 {
    padding: 3rem;
}

.p4 {
    padding: 4rem 1rem;
}

.p5 {
    padding: 5rem 2rem;
}

.title-diw {
    position: absolute;
    left: 0;
    right: 0;
    top: 20%;
    color: #ffe45e;
    text-align: center;
    font-size: 10rem;
    font-style: italic;
    -webkit-animation: move-up 2s forwards;
    animation-iteration-count: 3;
    animation: move-up 2s forwards;
    text-shadow: #ffe45e 0 0 30px, #e5c654 1px 0 10px;
}
